<template>
  <div class="dropdown">

  <!-- <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-expanded="false">
    Dropdown button
  </button>
  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
    <a class="dropdown-item" href="#">Item</a>
    <a class="dropdown-item" href="#">Another Item</a>
    <a class="dropdown-item" href="#">One more item</a>
  </div>
</div> -->

    <div class="btn-group" :class="rootClass">
        <button type="button" class="btn dropdown-toggle" :class="selectedItemNameBtnClass" data-toggle="dropdown">{{selectedItemName}}</button>
        <!-- <button
            type="button"
            class="btn dropdown-toggle dropdown-toggle-split"
            :class="toggleDropdownBtnClass"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
            >
            <span class="sr-only">Toggle Dropdown</span>
        </button> -->
        <div class="dropdown-menu" :class="menuDivClass" :style="scroll ? 'overflow-y: auto; max-height: ' + menuDivMaxHeight : ''">
            <a
                v-for="item in items"
                class="dropdown-item"
                style="cursor: pointer;"
                :class="{active: selectedItem === item}"
                @click="selectItem(item)"
                >{{item[displayAttr]}}</a>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                selectedItem: null
            }
        },
        props: {
            items: {
                type: Array,
                require: false,
                default: []
            },
            defaultSelectedIndex: {
                type: Number,
                require: false,
                default: 0
            },
            rootClass: {
                type: String,
                require: false,
                value: ''
            },
            selectedItemNameBtnClass: {
                type: String,
                require: false,
                default: 'btn-secondary'
            },
            toggleDropdownBtnClass: {
                type: String,
                require: false,
                default: 'btn-secondary'
            },
            menuDivClass: {
                type: String,
                require: false,
                default: ''
            },
            menuItemClass: {
                type: String,
                require: false,
                default: ''
            },
            scroll: {
                type: Boolean,
                require: false,
                default: false
            },
            menuDivMaxHeight: {
                type: String,
                require: false,
                default: '250px'
            },
            displayAttr: {
                type: String,
                require: false,
                default: 'displayName'
            }
        },
        methods: {
            selectItem (item) {
                if (item) {
                    this.selectedItem = item

                    this.$emit('selectedItem', this.selectedItem)
                }
            }
        },
        computed: {
            selectedItemName () {
                //return this.selectedItem ? this.selectedItem.displayName : ''
                return this.selectedItem ? this.selectedItem[this.displayAttr] : ''
            }
        },
        created () {
            this.selectItem(this.items[this.defaultSelectedIndex])
        }
    }
</script>

