<script>
    import Dropdown from '../../../components/index/Dropdown.vue'

    import request from '../../../lib/request'

    export default {
        name: 'app',
        components: {
            Dropdown
        },
        methods: {
          getLocation () {
            request.organ.getLocation()
            .then(result => {
              this.$log.debug('get locations ', result)

              if (result.data.result === 200) {
                // //this.treeData.children = result.data.list
                // // 1 depth
                // this.locations.children =result.data.list.filter(location => location.depth === 1)
                // .map(location => {
                //   // 2 depth
                //   location.children = result.data.list
                //   .filter(_location => {
                //     return _location.parentId === location.organId
                //   })
                //   .map(_location => {
                //     // 3 depth
                //     _location.children = result.data.list
                //     .filter(__location => {
                //       return __location.parantId === _location.organId
                //     })
                //
                //     return _location
                //   })
                //   return location
                // })


                // make location list
                result.data.list.forEach(_location => {
                  if(_location.depth == 1 || _location.depth == 2) // 2단계까지가 지점
                  {
                    _location.displayName = _location.name + ' (' + _location.organId + ')'
                    this.locations_list.push(_location)
                  }
                })



              } else {
                alert('지점 조회를 실패했습니다. (' + result.data.resultMsg + ')')
                // fail
              }
            })
            .catch(err => {
              alert('지점 조회를 실패했습니다. (' + err.message + ')')
            })
          },
            selectMonitorCount (monitorCount) {
                if (this.client.boards.length < monitorCount.value) {
                    // add
                    this.client.boards.splice(
                        this.client.boards.length,
                        0,
                        ...Array.from(
                            {
                                length: monitorCount.value - this.client.boards.length
                            },
                            (v, k) => {
                                return {
                                    boardType: '001',
                                    boardIndex: this.client.boards.length + k,
                                    resolutionId: 0,
                                    layoutType: 0
                                }
                            }))
                } else {
                    if (this.client.boards.length > monitorCount.value) {
                        // del
                        this.client.boards.splice(this.client.boards.length - (this.client.boards.length - monitorCount.value), this.client.boards.length - monitorCount.value)
                    }
                }
            },
            selectMonitorResolution (monitorResolution) {
                this.resolution = monitorResolution
            },
            addClient () {
              // Check validation
              //{{
              if (this.clientData.organId === '') {
                alert('지점ID를 입력하세요')
                return;
              }

              if (this.clientData.clientName === '') {
                alert('클라이언트명을 입력하세요')
                return;
              }

              //}}

                //if (confirm('클라이언트를 추가하시겠습니까?')) {
                    request.client.addClient(this.clientData)
                        .then(result => {
                            this.$log.debug('addClient result ', result)
                            this.clientId = result.data.clientId
                            confirm('클라이언트를 추가했습니다.')

                            return {
                                clientId: this.clientId,
                                requestUrl: result.data.requestUrl + '?clientId=' + this.clientId
                            }
                        })
                        .then(_configData => {
                            var configData = '[RegistrationInfo]'
                                configData+= 'ClientID=' + _configData.clientId + ','
                                configData+= 'ReqURL=' + _configData.requestUrl + ','
                                configData+= 'result=success'

                            var ext = window.external

                            ext.saveConfig(configData);

                            var returnvalue = ext.loadConfig();

                            // alert(returnvalue)

                            function loadConfig(msg)
                            {
                                // alert(msg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('addClient fail ', err)
                            alert('클라이언트 추가를 실패했습니다. (' + err.message + ')')
                        })
                //}
            },
            getResolution () {
                request.code.getResolution(parseInt(this.monitorResolutionType))
                    .then(result => {
                        this.$log.debug('getResolution ', JSON.stringify(result.data.list, null, '\t'))

                        //this.resolutions = result.data.list
                        this.resolutions.splice(0, this.resolutions.length)
                        this.resolutions.splice(0, 0, ...result.data.list)
                        this.$log.debug('getResolution ', this.resolutions)

                        let def_resolution = this.resolutions[0];
                        if(this.monitorResolutionType == 0) // 가로
                        {
                          this.resolutions.forEach(resolution => {

                            if(resolution.width == 1920 & resolution.height == 1080)
                            {
                                def_resolution = resolution
                            }
                          })

                        }
                        else { // 세로
                          this.resolutions.forEach(resolution => {

                            if(resolution.width == 1080 & resolution.height == 1920)
                            {
                                def_resolution = resolution
                            }
                          })
                        }
                        this.$refs.resolutionDropdown.selectItem(def_resolution) //this.resolutions[0])
                    })
                    .catch(err => this.$log.error('getResolution error ', err))
            }
        },
        watch: {
            monitorResolutionType (oldVal, newVal) {
                this.getResolution()
            }
        },
        data () {
            return {
              // locations: {
              //   name: '지점목록',
              //   children: [
              //   ]
              // },
              locations_list: [
              ],
              monitorCounts: Array.from(
                    {
                        length: 6
                    },
                    (k, v) => {
                        let count = v + 1

                        return {
                            displayName: count,
                            value: count
                        }
                    }),
                resolutions: [
                ],
                contentTypes: [
                    {
                        displayName: '공고문',
                        value: '001'
                    },
                    {
                        displayName: '홍보용',
                        value: '002'
                    }
//                    ,
//                    {
//                        displayName: '입간판'
//                    },
//                    {
//                        displayName: '자동'
//                    }
                ],
                monitorResolutionType: '1',
                client: {
                    organId: '',
                    clientName: '',
                    boards: []
                },
                resolution: null,
                clientId: ''
            }
        },
        computed: {
            clientData () {
                this.client.boards.forEach(board => {
                    board.resolutionId = this.resolution.resolutionId
                    board.layoutType = parseInt(this.monitorResolutionType)
                })

                return this.client
            }
        },
        created () {
            this.getLocation()
            this.getResolution()
        }
    }
</script>

